import { Component, OnInit, Input, Output, EventEmitter, Injector } from "@angular/core";
import { Observable } from "rxjs/Observable";
import { BehaviorSubject } from "rxjs/BehaviorSubject";

import { DiagramService } from "../../services/diagram.service";
import { DiagramModel } from "../../models";

@Component({
	selector: "numeric-edit-diagram",
	template: `<mat-card *ngIf="dataLoaded | async" class="diagram-box">
  <mat-card-header>
    <mat-card-title fxLayoutAlign="space-between center">
    </mat-card-title>
  </mat-card-header>
  <mat-card-content>
  </mat-card-content>
</mat-card>`,
	styles: [`:host{width:100%}.diagram-box{margin:15px}.numeric{font-size:3em;line-height:1.8;font-family:iran-sans-bold,Tahoma}.numeric.title{font-size:1.5em}`]
})
export class NumericEditDiagramComponent implements OnInit {
	dataLoaded = new BehaviorSubject(false);
	_data;
	@Input()
	set data(data) {
		this._data = data;
		this.dataLoaded.next(true);
	}
	get data() {
		return this._data;
	}
	constructor(private injector: Injector, private diagramService: DiagramService) {
		this.data = this.injector.get("data");
	}

	ngOnInit() {
		// this.dataSubscribtion = this.diagramService.getData(this.data.Source.Route)
		this.diagramService.getData(this.data.Source).subscribe(data => {
			debugger;
		});
	}
}
